import React from 'react'
import PropTypes from 'prop-types'
import { navigate } from 'gatsby'
import { Col, Nav } from 'react-bootstrap'
import './Sidebar.scss'
import Author from './Author'
import Badge from 'react-bootstrap/Badge'
import useTags from '../hooks/useTags'

const Sidebar = ({ selected = 'post', selectedTag = '' }) => {
  const { tags } = useTags()
  return (
    <Col lg={3} className={'sidebar'}>
      <Author/>
      <h5 className={selected === 'post' ? 'mt-4 pointer active' : 'mt-4 pointer'} onClick={() => navigate('/')}></h5>
      <h5 className={selected === 'series' ? 'mt-4 pointer active' : 'mt-4 pointer'}
          onClick={() => navigate('/series')}></h5>
      <h5 className={selected === 'tag' ? 'mt-4 active' : 'mt-4'}></h5>
      <hr/>
      <Nav className="flex-column">
        {tags.map(
          ({ fieldValue, totalCount }, index) =>
            <Nav.Item key={index}>
              <Nav.Link onClick={() => navigate(`/tag/${fieldValue}`)}
                        active={fieldValue === selectedTag}>
                {fieldValue} <Badge pill variant="light">{totalCount}</Badge>
              </Nav.Link>
            </Nav.Item>
        )}
        {/*<Nav.Item><Link to="/categories/">Categories</Link></Nav.Item>*/}
        {/*<Nav.Item><Link to="/tags/">Tags</Link></Nav.Item>*/}
      </Nav>
      <hr/>
      <div>
        <span className="social-icon">a</span>
      </div>
    </Col>
  )
}

Sidebar.propTypes = {
  selected: PropTypes.string,
  selectedTag: PropTypes.string,
}

export default Sidebar